import React,{useState} from 'react'
import './Todo.css'
function Todo() {
    const[todos, setTodos] = useState([])
    const[todo, setTodo] = useState('')

    const storeItem = (e)=>{
        e.preventDefault()
        setTodo('')
    }
   


    const removeItem = (key)=>{
        setTodos(todos.filter((data,index)=>index !== key))
    }


    console.log(todo);
    console.log(todos);

    return (
        
        <div>
        <form   onSubmit={storeItem} className="todo">

          <h1 className="todo-tittle">Todo List</h1>
          <input className="todo-input" value={todo} onChange={(e)=>setTodo(e.target.value)} placeholder=" Enter Items" type="text"></input>
          <button disabled={!todo} onClick={()=>setTodos([...todos,todo])} className=" todo-i ">Add Item</button>
         
        </form >

        <ul> {todos.map((data,index)=>{
            return(
                <li key={index}>{data}<i onClick={()=>removeItem(index)} className='fas fa-trash-alt'></i></li>
            )
        })}</ul>
        

        </div>
    )
}

export default Todo
